
import Vue from 'vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'

import { setOwnInfo } from '@/api/user'
import { pca, pcaa } from 'area-data'
import VueAreaLinkage from 'vue-area-linkage'
import 'vue-area-linkage/dist/index.css'
Vue.use(VueAreaLinkage)
export default {
name: 'integralGet',
components: {
XInput,
Group,
XButton,
XAddress,
ChinaAddressData,
Checklist,
Checker,
CheckerItem,
pca,
pcaa
},
data () {
return {
userInfo: {
ageinfo: [],
city: [],
wayrand: []
},
selected2:[],
pcaa:pcaa,
addressData: ChinaAddressData,
addressValue: ['广东省', '广州市', '天河区'],
cityVal: [],
ageChecked: [],
wayChecked: [],
getaddres: []
}
},
computed: {
},
created () {
setOwnInfo()
.then(res => {
if (res.data.errorcode === 0) {

this.userInfo = res.data.data

let newArr = []
this.userInfo.ageinfo.map((item, index) => {
const obj = {
key: item.title,
value: item.title
}
newArr.push(obj)
if (item.isseled === 1) {

this.ageChecked = index
}
})
this.userInfo.ageinfo = newArr

let newArr2 = []
this.userInfo.wayrand.map((item, index) => {
const obj = {
key: item.title,
value: item.title
}
newArr2.push(obj)
if (item.isseled === 1) {

this.wayChecked = index
}
})
this.userInfo.wayrand = newArr2

let oldaddress = res.data.data.city
oldaddress = oldaddress.split(' ')
this.selected2 = oldaddress
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
methods: {

handleSubmit () {




let postData = {
name: this.userInfo.name,
age: this.ageChecked,
way: this.wayChecked,
city: this.selected2.join(' ')
}
setOwnInfo(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('修改成功', 'middle')
this.$router.push({
path: '/usercenter'
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleLoginout () {
this.$router.push({
path: '/usercenter'
})
},
onShadowChange (ids, names) {
this.getaddres = names
}
}
}



